.flower-cont {
  height: 100%;
  position: absolute;

}

@media only screen and (min-width: 651px) {
  .flower-cont{
    left: 5%;
    top: 2%;
    width: 50%;
  }
}

@media only screen and (max-width: 650px) {
  .flower-cont {
    left: 10%;
    top: 0%;
    width: 80%;
  }
}

